#projects-container {
    min-height: 100vh;
    margin-top: 100px;
    h2, h3 {
        text-align: center;
    }
    h2 {
        margin-bottom: 5%;
    }
    .projects {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        gap: 10px;
        width: 100%;
        max-width: 1200px;
        margin: auto;
        .cards-container {
            position: relative;
            h3 {
                color: $black;
                text-decoration: underline 3px $color-4;
            }
            p {
                width: 80%;
                text-align: center;
            }
            .project-card {
                height: 380px;
                border: 1px solid $black;
                border-radius: 10px;
                width: 580px;
                @include center-child;
                flex-direction: column;
                gap: 10px;
                position: relative;
                background-color: $color-2;
                cursor:url('../../../public/img/cursor/Plus\ Symbol.cur'), auto;
            }
            .project-card-back {
                height: 380px;
                border: 1px solid $black;
                border-radius: 50px;
                width: 580px;
                background-color: $color-1;
                @include center-child;
                flex-direction: column;
                gap: 20px;
                position: absolute;
                top: 0;
                cursor:url('../../../public/img/cursor/Minus\ symbol.cur'), auto;
                .btn-link {
                    color: $white;
                    background-color: $color-4;
                    border-radius: 20px;
                    padding: 10px;
                }
            }
            .d-none {
                display: none;
            }
        }
        @media (max-width: 1200px) {
            grid-template-columns: 1fr;
            .cards-container {
                justify-self: center;
                .project-card-back {
                    position: unset;
                }
            }
        }
        @media (max-width: 600px) {
            .cards-container {
                .project-card, .project-card-back {
                    width: 350px; 
                    img {
                        max-width: 100%;
                        height: auto;
                    }
                }
            }
        }
    }
}
